@font-face {
    font-family: 'geo_sans_light';
    src: local('geo_sans_light'), url(../../assets/fonts/geo_sans_light/GeosansLight.ttf) format('ttf');
}

.app{
    font-family: "Lato", "sans-serif";
    height: 100%;
    width: 100%;
    padding: 0px;
    margin: 0px;
}

.container {
    margin-top: 64px;
}
.line-break {
    height: 1px;
    width: 90%;
    margin: 20px auto;
    background-color: rgb(200, 200, 200);

}
.hidden {
    display: none;
}
.card {
    border-radius: 10px;
    box-shadow: 0 10px 20px 0 RGBA(0, 0, 0, 0.4);
}